import React, {useState, useEffect} from 'react';

const PlayListV2 = () => {
  const [data, setData] = useState(null)
  const fetchURL = "http://localhost/php_api_test/apiBasic/read_playlist.php"
  const getData = () =>
    fetch(`${fetchURL}/posts`)
      .then((res) => res.json())

  useEffect(() => {
    getData().then((data) => setData(data))
  }, [])

  return (
    <>
    <h1>Dit is voor de beheerder (data uit database)</h1>
    <table border="1">
     {data?.map((item,index) => 
        
        <tr>
         {index==0?
            <>
              { Object.keys(item).map((keyName, i) => (
                  <td className = 'messageColHead'>  {[keyName]}</td>
                ))
              }
            </> : 
            <></>
            }
        </tr>


      )}

      {data?.map((item,index) => 
        <tr>
          { Object.keys(item).map((keyName, i) => (
              <td className = 'messageCol'>  {item[keyName]}</td>
            ))
          }
        </tr>
      )
      }
    </table>
    </>
  )
}



export default PlayListV2;