import React, {useState, useEffect} from 'react';

const PlayListV2 = () => {
  const [data, setData] = useState(null)
  const fetchURL = "http://localhost/php_api_test/apiBasic/read_playlist.php"
  const getData = () =>
    fetch(`${fetchURL}/posts`)
      .then((res) => res.json())

  useEffect(() => {
    getData().then((data) => setData(data))
  }, [])

  return (
    <table border="1" className="songlist">

      {//  id	  order1	  title	  artist	  afspraken	  videoURL1	  musicSheetURL1	  musicSheetURL2	  image1	  image2
      }
    
      <tr>
        <th className = 'SLTH nr'>Nr </th>
        <th className = 'SLTH'> Song </th>
        <th className = 'SLTH'> Afspraak </th>
        <th className = 'SLTH'> Video/Muziek</th>
      </tr> 

      {data?.map((item,index) => 
        <tr>
           <td className = 'nr'> {item['order1']}</td>
          <td className = 'width40'>{item['title']}<br/>{item['artist']} </td>
          <td> {item['afspraken']}</td>
          <td> 
            {item['videoURL1']?<tr><a href ={ item['videoURL1']} target="_blank" >video1</a> </tr>:''}
            {item['videoURL2']?<tr><a href ={ item['videoURL2']} target="_blank" >video2</a> </tr>:''}
       
              {item['musicSheetURL1']?<tr><a href ={ item['musicSheetURL1']} target="_blank" >bladmuziek1</a> </tr>:''}
              {item['musicSheetURL2']?<tr><a href ={ item['musicSheetURL2']} target="_blank" >bladmuziek2</a> </tr>:''}  
            </td>
        </tr>
      )
      }
    </table>
  )
}

export default PlayListV2;